<template>
    <div>
        <div id="header">
            <div class="logo">
                <a href="https://www.iiiexhibition.com/"
                    ><img src="../assets/logo.png"
                /></a>
            </div>
            <div class="back_to_lobby" id="backToLobbyBtn">
                <p>作品一覧へ→</p>
            </div>
        </div>
        <div id="contents_middle">
            <div id="works">
                <div id="works_window" class="left_column">
                    <div
                        v-if="currentWorks"
                        v-html="currentWorks.worksHTML"
                    ></div>
                </div>
                <div id="works_detail" class="left_column">
                    <div id="works_title">
                        <div v-if="currentWorks">
                            <h2>{{ currentWorks.title }}</h2>
                        </div>
                    </div>
                    <div id="creators">
                        <div v-if="currentWorks">
                            <p>{{ currentWorks.creators.join(', ') }}</p>
                        </div>
                    </div>
                    <div id="description">
                        <div
                            v-if="currentWorks"
                            v-html="currentWorks.descriptionHTML"
                        ></div>
                    </div>
                </div>
                <div
                    id="questionaire_container"
                    class="left_column"
                    v-if="currentWorks && currentWorks.questionaire_url"
                >
                    <div id="questionaire_header">
                        <p style="font-size:13px;">
                            <b>アンケートに回答する</b>
                        </p>
                    </div>

                    <iframe
                        :src="`${currentWorks.questionaire_url}`"
                        width="100%"
                        :height="`${currentWorks.questionaire_height}`"
                        frameborder="0"
                        marginheight="0"
                        marginwidth="0"
                        >読み込んでいます…</iframe
                    >
                </div>
            </div>
            <div class="pc" id="right_col_container">
                <div class="pc" id="nav_and_react_container">
                    <div id="nav_pc">
                        <div id="seen_pc">
                            <p>
                                <b>閲覧済み </b>:
                                <b style="color: red">{{ seenCount }}</b>
                                <span style="font-size: 15px">
                                    / {{ works.length }}</span
                                >
                            </p>
                        </div>
                        <div id="next_and_prev_pc">
                            <div id="next_pc">
                                <p><b style="font-size: 13px">次の作品</b></p>
                                <div
                                    id="next_img"
                                    class="next_and_prev_img_pc"
                                    v-on:click="
                                        gotoOtherWorks(nextWorks.worksId)
                                    "
                                >
                                    <img
                                        v-if="nextWorks"
                                        :src="
                                            require(`@/assets/thumbnails/${nextWorks.worksId}.png`)
                                        "
                                    />
                                    <div v-else></div>
                                </div>
                            </div>
                            <div id="prev_pc">
                                <p><b style="font-size: 13px">前の作品</b></p>
                                <div
                                    id="prev_img"
                                    class="next_and_prev_img_pc"
                                    v-on:click="
                                        gotoOtherWorks(prevWorks.worksId)
                                    "
                                >
                                    <img
                                        v-if="prevWorks"
                                        :src="
                                            require(`@/assets/thumbnails/${prevWorks.worksId}.png`)
                                        "
                                    />
                                    <div v-else></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div id="react_pc">
                        <div id="react_header">
                            <div>
                                <b style="font-size: 13px"
                                    >他の人のリアクション</b
                                >
                            </div>
                            <div
                                class="mt-normal-violet"
                                style="font-size: 5px; margin-left: 5px"
                            >
                                <input
                                    id="react_toggle"
                                    type="checkbox"
                                    v-on:change="handleToggleChange"
                                />
                                <label for="react_toggle"></label>
                            </div>
                        </div>

                        <div id="react_window_container" class="active">
                            <div id="react_window" class="active"></div>
                        </div>
                    </div>
                    <div class="react_btns_container">
                        <div>
                            <b style="font-size: 13px">リアクションを送る</b>
                        </div>
                        <div class="react_btns">
                            <div
                                class="reactBtn"
                                id="ReactButton_1"
                                ontouchstart=""
                            >
                                <img
                                    src="../assets/buttons/Emoji_1.png"
                                    class="BtnImg"
                                />
                            </div>
                            <div class="reactBtn" id="ReactButton_2">
                                <img
                                    src="../assets/buttons/Emoji_2.png"
                                    class="BtnImg"
                                    ontouchstart=""
                                />
                            </div>
                            <div class="reactBtn" id="ReactButton_3">
                                <img
                                    src="../assets/buttons/Emoji_3.png"
                                    class="BtnImg"
                                    ontouchstart=""
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import io from 'socket.io-client';
import { mapState, mapMutations, mapActions } from 'vuex';
export default {
    data() {
        return {
            currentWorks: null,
            nextWorks: null,
            prevWorks: null,

            socket: io(`${process.env.VUE_APP_APIURL}`),
            members: [],
            lobby_path: 'https://yahoopuyo.github.io/iiie2021_UX/',

            header: null,
            contents_middle: null,
            worksElem: null,
            worksWindowElem: null,
            nav_and_react_container: null,
            react_window: null,
            react_window_container: null,
            toggle: null,
        };
    },
    computed: {
        ...mapState(['works', 'idOrder', 'history']),
        seenCount() {
            return this.works.filter(work => work.seen).length;
        },
    },
    watch: {
        '$route.params.worksId': function(val, oldval) {
            if (val !== undefined) {
                this.initMembers();
                this.socket.emit('join-room', {
                    roomId: val,
                });
                this.updateWorks();
                this.updateHistory({ worksId: val });
                //console.log(`moved! ${this.members}`);
            }
        },
    },
    methods: {
        ...mapMutations(['initHistory']),
        ...mapActions(['getAndSetHistory', 'updateHistory']),
        createMember(n_memberId) {
            if (n_memberId !== this.socket.id) {
                const people = document.createElement('div');
                const peopleImg = document.createElement('img');
                const peopletype = Math.floor(Math.random() * 8) + 1;
                const gray = Math.random() * 3 + 1;
                const imgsrc = require(`@/assets/people/${peopletype}.png`);
                peopleImg.setAttribute('src', imgsrc);
                people.setAttribute('id', n_memberId);
                people.classList.add('member');
                people.style.position = 'absolute';
                people.style.bottom = '0px';
                peopleImg.style.filter = `grayScale(1) brightness(${gray})`;
                people.appendChild(peopleImg);
                this.react_window.appendChild(people);
                setTimeout(() => {
                    people.classList.add('exist');
                }, 10);
            }
        },
        deleteMember(n_memberId) {
            const people = document.getElementById(n_memberId);
            if (people) {
                /*people.classList.add('gone');
                setTimeout(() => {
                    this.react_window.removeChild(people);
                }, 600);*/
                this.react_window.removeChild(people);
            }
        },
        updateMemberPos(members) {
            const all = members.length;
            var idx = 1;
            members.map(member => {
                const membersElement = document.getElementById(member);
                if (membersElement) {
                    const offset = (idx / all) * 100;
                    membersElement.style.left = `calc(${offset}% - ${(this
                        .react_window_container.offsetHeight *
                        0.6 *
                        2.5) /
                        7}px)`;
                    membersElement.childNodes[0].style.height = `${this
                        .react_window_container.offsetHeight * 0.6}px`;
                    idx += 1;
                }
            });
        },
        initMembers() {
            this.members = [];
            while (this.react_window.firstChild) {
                this.react_window.removeChild(this.react_window.firstChild);
            }
        },
        updateMembers(n_members) {
            n_members.map(member => {
                if (!this.members.includes(member)) this.createMember(member);
            });
            this.members.map(member => {
                if (!n_members.includes(member)) this.deleteMember(member);
            });
            this.updateMemberPos(n_members);
            this.members = n_members;
            //console.log(`updated ${this.members}`);
        },
        createMyReaction(reactionId) {
            const reactionButtonElement = document.getElementById(
                `ReactButton_${reactionId}`
            );
            const w = reactionButtonElement.offsetWidth;

            const reaction = document.createElement('img');
            const imgsrc = require(`@/assets/buttons/Emoji_${reactionId}.png`);
            reaction.setAttribute('src', imgsrc);
            reaction.classList.add('myReaction');
            reaction.style.display = 'block';
            reaction.style.position = 'absolute';
            reaction.style.top = '0px';
            reaction.style.left = `${w * 0.15}px`;
            reaction.style.width = `${w * 0.7}px`;
            reaction.style.zIndex = '10';
            reactionButtonElement.appendChild(reaction);
            setTimeout(() => reaction.classList.add('disappeared'), 10);
            setTimeout(() => reactionButtonElement.removeChild(reaction), 1500);
        },
        createOthersReaction(senderId, reactionId) {
            const senderElement = document.getElementById(senderId);
            const reaction = document.createElement('img');
            const imgsrc = require(`@/assets/buttons/Emoji_${reactionId}.png`);
            reaction.setAttribute('src', imgsrc);
            reaction.classList.add('othersReaction');
            reaction.style.display = 'block';
            reaction.style.position = 'absolute';
            reaction.style.top = '20px';
            const w = senderElement.offsetWidth;
            reaction.style.left = `${w / 2 - 10}px`;
            reaction.style.width = '20px';
            reaction.style.zIndex = '10';
            senderElement.appendChild(reaction);
            setTimeout(() => reaction.classList.add('disappeared'), 10);
            setTimeout(() => senderElement.removeChild(reaction), 1500);
        },
        calc_margintop() {
            /*if (
                this.contents_middle.offsetHeight <
                window.innerHeight - this.header.offsetHeight
            ) {
                this.contents_middle.style.marginTop = `${Math.max(
                    (window.innerHeight - this.contents_middle.offsetHeight) /
                        3,
                    50
                )}px`;
            } else {
                this.contents_middle.style.marginTop = '50px';
            }*/
            this.contents_middle.style.marginTop = '55px';
        },
        style_works_height() {
            const aspect_ratio = this.currentWorks.aspect_ratio
                ? this.currentWorks.aspect_ratio
                : 16 / 9;
            this.worksWindowElem.style.height = `${this.worksWindowElem
                .offsetWidth / aspect_ratio}px`;
        },
        style_right_col() {
            this.nav_and_react_container.style.maxHeight = `${Math.min(
                this.worksElem.offsetHeight,
                500
            )}px`;
        },
        update_style() {
            this.style_works_height();
            this.style_right_col();
            this.calc_margintop();
        },
        handleToggleChange(e) {
            if (e.target.checked) {
                this.react_window.classList.add('active');
                this.react_window_container.classList.add('active');
            } else {
                this.react_window.classList.remove('active');
                this.react_window_container.classList.remove('active');
            }
        },
        updateWorks() {
            this.currentWorks = this.works.filter(work => {
                return work.worksId === this.$route.params.worksId;
            })[0];
            var cid = this.idOrder.indexOf(this.currentWorks.worksId);
            var id1 = cid - 1;
            if (id1 === -1) id1 = this.idOrder.length - 1;
            var id2 = cid + 1;
            if (id2 == this.idOrder.length) id2 = 0;
            this.prevWorks = this.works.filter(work => {
                return work.worksId === this.idOrder[id1];
            })[0];
            this.nextWorks = this.works.filter(work => {
                return work.worksId === this.idOrder[id2];
            })[0];
        },
        gotoOtherWorks(id) {
            this.$router.push(`/${id}`);
        },
    },
    mounted() {
        this.initHistory();
        this.getAndSetHistory();
        this.header = document.getElementById('header');
        this.contents_middle = document.getElementById('contents_middle');
        this.worksElem = document.getElementById('works');
        this.worksWindowElem = document.getElementById('works_window');
        this.nav_and_react_container = document.getElementById(
            'nav_and_react_container'
        );
        this.react_window = document.getElementById('react_window');
        this.react_window_container = document.getElementById(
            'react_window_container'
        );
        this.toggle = document.getElementById('react_toggle');
        this.updateWorks();
        this.updateHistory({ worksId: this.$route.params.worksId });
        const reactionButtonElements = document.getElementsByClassName(
            'reactBtn'
        );
        for (var i = 0; i < reactionButtonElements.length; i++) {
            const reactionId = reactionButtonElements[i].id
                .split('_')
                .slice(-1)[0];
            reactionButtonElements[i].addEventListener('click', () => {
                this.socket.emit('reaction', { reactionId: reactionId });
                this.createMyReaction(reactionId);
            });
        }
        this.update_style();
        this.toggle.checked = true;

        document
            .getElementById('backToLobbyBtn')
            .addEventListener('click', () => {
                window.location.href = this.lobby_path;
            });

        window.addEventListener('resize', () => {
            this.updateMemberPos(this.members);
            this.update_style();
        });
        //socket通信が確立したタイミングで、サーバーに対して一度メンバーリストを送信するように要求を行う
        this.socket.on('connect', () => {
            console.log(`connected! ${this.socket.id}`);
            this.socket.emit('join-room', {
                roomId: this.$route.params.worksId,
            });
        });

        //サーバーから個別に部屋のメンバーリストを受け取る(通信が確立した時)
        this.socket.on('members-private', props => {
            const received_members = JSON.parse(props.members);
            //console.log(`joined ${props.roomId}`);
            this.updateMembers(received_members);
            //console.log(`received ${received_members}`);
        });

        //同じ部屋に他の人が入ったした時に、メンバーリストと、新たに参加したメンバーのIDを受け取る
        this.socket.on('members-joined', props => {
            const received_members = JSON.parse(props.members);
            this.updateMembers(received_members);
        });

        //同じ部屋の他の人が離脱した時に、メンバーリストと、離脱したメンバーのIDを受け取る
        this.socket.on('members-left', props => {
            const received_members = JSON.parse(props.members);
            this.updateMembers(received_members);
        });

        this.socket.on('members-reaction', props => {
            this.createOthersReaction(props.senderId, props.reactionId);
        });
    },
    updated() {
        this.update_style();
    },
    beforeDestroy() {
        this.socket.disconnect();
    },
};
</script>
<style lang="css">
/* パソコンで見たときは"pc"のclassがついたDOMが表示される */
@media only screen and (min-width: 750px) {
    .sp {
        display: none !important;
    }
}

/* スマートフォンで見たときは"sp"のclassがついたDOMが表示される */
@media only screen and (max-width: 750px) {
    .pc {
        display: none !important;
    }
}

#header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 30px;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 999;

    background-color: rgba(10, 10, 50, 0.8);
}

.logo {
    height: 100%;
}
.logo img {
    height: 100%;
    margin-left: 10px;
}
.back_to_lobby {
    color: white;
    margin-right: 15px;
}
.back_to_lobby:hover {
    border-bottom: 1px solid white;
    cursor: pointer;
}

#contents_middle {
    margin-left: auto;
    margin-right: auto;
    width: min(100vw, 200vh);
    max-width: 95%;
    margin-bottom: 30px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}
#works {
    display: flex;
    flex: 1;
    max-width: 1200px;
    min-width: 300px;
    flex-direction: column;
    align-items: center;
    /*background-color: red;*/
}
.left_column {
    max-width: calc((100vh - 150px) * 16 / 9);
}
#works_window {
    width: 95%;
    margin-top: 5px;
    /*aspect-ratio: 16 / 9;*/
    /*background-color: gray;*/
    border-radius: 10px;
    margin-bottom: 5px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: rgba(141, 141, 141, 0.25) 0px 54px 55px,
        rgba(122, 122, 122, 0.12) 0px -12px 30px,
        rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px,
        rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
#works_window > div {
    width: 100%;
    height: 100%;
}
#works_detail {
    margin-top: 20px;
    margin-bottom: 0px;
    width: 90%;
    background-color: white;
    padding: 10px;
    border-radius: 10px;
    box-shadow: rgba(141, 141, 141, 0.25) 0px 54px 55px,
        rgba(122, 122, 122, 0.12) 0px -12px 30px,
        rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px,
        rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
#works_title {
    font-size: 13px;
    line-height: 25px;
}
#creators {
    margin-top: 5px;
    font-size: 15px;
    word-break: keep-all;
}
#description {
    margin-top: 10px;
    font-size: 12px;
}
#questionaire_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#questionaire_header {
    margin: 20px 10px 0px 10px;
}

#right_col_container.pc {
    max-width: 300px;
    flex: 0.4;
    padding: 0px 10px 0px 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

#nav_and_react_container.pc {
    width: 100%;
    height: max(70vh, 300px);
    padding: 0px 10px 0px 10px;
    /*height: min(calc(min(100vw - 410px, 940px) * 9 / 16 + 30px), 80vh);*/
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    /*background-color: blue;*/
}
#seen_pc {
    margin-bottom: 5px;
    font-size: 20px;
}

#next_and_prev_pc {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 10px;
    width: 90%;
}
.next_and_prev_img_pc {
    box-shadow: 1px 0 3px 0 rgba(0, 0, 0, 0.12), 0 2px 3px 0 rgba(0, 0, 0, 0.22);
    transition: all 0.3s;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.next_and_prev_img_pc img {
    width: 110%;
}
.next_and_prev_img_pc div {
    width: 100%;
    height: 100%;
    background: rgb(216, 216, 216);
}
.next_and_prev_img_pc:hover {
    cursor: pointer;
    box-shadow: 0 15px 30px -5px rgba(0, 0, 0, 0.507),
        0 0 5px rgba(0, 0, 0, 0.1);
    transform: translateY(-2px);
}
#next_pc {
    flex: 1;
    max-width: 80%;
}
#prev_pc {
    margin-left: 10px;
    flex: 0.6;
}
#next_img {
    width: 100%;
    aspect-ratio: 16 / 9;
    border-radius: 10px;
    margin-top: 2px;
}
#prev_img {
    width: 100%;
    aspect-ratio: 16 / 9;
    border-radius: 10px;
    margin-top: 3px;
}

#react_pc {
    height: 30%;
}

#react_header {
    display: flex;
    align-items: center;
}

#react_window_container {
    height: min(calc(100% - 30px), 120px);
    margin-top: 5px;
    border-radius: 10px;
    position: relative;
    background-color: rgb(216, 216, 216);
    background-image: linear-gradient(
        to right top,
        transparent 49.5%,
        rgb(124, 124, 124) 49.5%,
        rgb(124, 124, 124) 50%,
        transparent 50%
    );
    transition: all 0.5s;
}

#react_window {
    transition: all 0.5s;
    opacity: 0;
}

#react_window_container.active {
    background-image: none;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em,
        rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em,
        rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
}

#react_window.active {
    opacity: 1;
}

.member {
    opacity: 0;
    transition: all 1s;
}

.member.exist {
    opacity: 1;
}

.member.gone {
    opacity: 0;
}
.react_btns {
    display: flex;
    margin-top: 3px;
}

.reactBtn {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    max-width: 35px;
    max-height: 35px;
    width: 7vw;
    height: 7vw;
    margin: 5px;
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-size: 18px;
    /*background-color: #008000; ←元の背景色は不要なので削除*/
    border-radius: 20%;
    text-align: center;
    box-shadow: 0 6px 5px 0px #838383;
    transform: translateY(-6px);
    transition: 0.1s all;
    overflow: visible;

    /*↓コピーしたCSSをここに追加↓*/
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#0ba500+0,008000+100 */
    background: #ffffff; /* Old browsers */
}

.reactBtn:hover {
    cursor: pointer;
}

.reactBtn:active {
    box-shadow: 0 2px 0 0 #6e6e6e;
    transform: translateY(-2px);
}

.BtnImg {
    display: block;
    width: 80%;
}
.myReaction {
    transition: all 1s ease-out;
}

.myReaction.disappeared {
    transform: translateY(-10vh);
    opacity: 0;
}

.othersReaction {
    transition: all 1s ease-out;
}

.othersReaction.disappeared {
    transform: translateY(-5vh);
    opacity: 0;
}
</style>
